.intro {
  $self: &;

  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  min-height: em(800, $fz_base);

  &__image {
    @include fill-img;
  }

  &__container {
    position: relative;
    z-index: 2;
    margin-top: auto;
    margin-bottom: auto;
    text-align: right;
  }

  &__title {
    text-align: center;
    line-height: 1;
    font-weight: 700;
    max-width: em(700, $fz_base);
    margin-left: auto;
    margin-right: auto;
    text-transform: uppercase;

    #{$self}__label {
      $fz: 65;
      font-size: em($fz, $fz_base);
    }
  }

  &__subtitle {
    text-align: center;
    line-height: 1.2;
    max-width: em(700, $fz_base);
    margin-left: auto;
    margin-right: auto;
    margin-top: em(10, $fz_base);

    #{$self}__label {
      $fz: 22;
      font-size: em($fz, $fz_base);
    }
  }

  &__title,
  &__subtitle {
    @include screen(md,max) {
      margin-right: 0;
    }
  }
}
