.modal-form {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: em(10, $fz_base);
  background-color: #fff;
  padding: em(100 50, $fz_base);
  z-index: 10;
  width: em(450, $fz_base);
  height: em(300, $fz_base);
  max-width: 100%;
  align-items: center;
  justify-content: center;

  &.is-active {
    display: flex;
  }

  &.is-thanks {
    .modal-form__msg {
      position: static;
      opacity: 1;
      transition: opacity 0.5s ease-in;
      z-index: 1;
    }

    form {
      display: none;
    }
  }

  &__msg {
    position: absolute;
    font-family: $font-second;
    text-align: center;
    opacity: 0;
    transition: opacity 0s;
    z-index: -1;

    .modal-form__label {
      $fz: 24;
      font-size: em($fz, $fz_base);
    }
  }

  &__close {
    position: absolute;
    top: em(5, $fz_base);
    right: em(5, $fz_base);
    width: em(30, $fz_base);
    height: em(30, $fz_base);
    cursor: pointer;

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      width: 100%;
      height: 2px;
      background-color: $color-brend;
    }

    &::before {
      transform: translateY(-50%) rotate(45deg);
    }

    &::after {
      transform: translateY(-50%) rotate(-45deg);
    }
  }

  form {
    display: flex;
    flex-direction: column;
    width: 100%;

    input {
      border: 1px solid $color-brend;
      border-radius: em(15, $fz_base);
      padding: em(10, $fz_base);

      &:not(:first-child) {
        margin-top: em(20, $fz_base);
      }

      &:last-child {
        margin-top: em(30, $fz);
      }
    }

    .button {
      justify-content: center;
      text-align: center;
    }
  }
}