.games {
  $self: &;
  padding-bottom: em(80, $fz_base);

  &__header {
    text-align: center;
    background-color: #000228;
    padding: em(30 0, $fz_base);

    &:not(:first-child) {
      margin-top: em(70, $fz_base);
    }
  }

  &__title {
    text-transform: uppercase;
    font-weight: 500;

    #{$self}__label {
      $fz: 50;
      font-size: em($fz, $fz_base);

      @include screen(md,max) {
        $fz: 24;
        font-size: em($fz, $fz_base);
      }
    }
  }

  &__subtitle {
    text-transform: uppercase;
    font-weight: 400;
    color: $color-brend;

    #{$self}__label {
      $fz: 22;
      font-size: em($fz, $fz_base);

      @include screen(md,max) {
        $fz: 24;
        font-size: em($fz, $fz_base);
      }
    }
  }

  &__grid {
    margin-top: em(50, $fz_base);
  }
}

.games-card {
  $self: &;

  position: relative;
  display: block;
  border-radius: em(40, $fz_base);
  overflow: hidden;
  transition: box-shadow $ease-main;
  cursor: pointer;

  @include screen(md,min) {
    &:hover {
      box-shadow: em(0 0 70 5, $fz_base) $color-brend;

      #{$self}__play {
        opacity: 1;
        transform: translate3d(-50%, -50%, 0) rotate(0deg);
      }
    }
  }

  &__play {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    right: 0;
    bottom: 0;
    left: 50%;
    transform: translate3d(-50%, -50%, 0) rotate(45deg);
    width: em(120, $fz_base);
    height: em(120, $fz_base);
    z-index: 2;
    border-radius: 100%;
    fill: $color-brend;
    box-shadow: em(10 10 70 20, $fz_base) #000;
    opacity: 0;
    transition: transform 0.2s $ease-main , opacity $ease-main;
  }

  &__bg {
    position: relative;
    overflow: hidden;
    @include ratio(398, 398);
  }

  &__image {
    @include fill-img;
  }
}
