.navigation {
  &__burger {
    @include screen(md,min) {
      display: none;
    }
  }
}

.burger {
  $self: &;

  position: relative;
  width: em(50, $fz_base);
  height: em(25, $fz_base);
  z-index: 5;

  &__line {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #fff;
    transition: width $ease-main, transform $ease-main;

    &--top {
      top: 0;

      #{$self}.is-active & {
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
      }
    }

    &--bottom {
      width: 70%;
      bottom: 0;

      #{$self}.is-active & {
        width: 100%;
        bottom: 50%;
        transform: translateY(50%) rotate(-45deg);
      }
    }
  }
}

.navigation-list {
  $self: &;

  display: flex;
  align-items: center;

  @include screen(md,max) {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: $color-brend;
    flex-direction: column;
    align-items: flex-start;
    padding: em(100 20 50 100, $fz_base);
    z-index: 4;
    transform: translateX(-150%);
    transition: transform $ease-main;

    &.is-active  {
      transform: translateX(0);
    }
  }

  &__item {
    @include screen(md,min) {
      &:not(:first-child) {
        margin-left: em(45, $fz_base);
      }
    }

    @include screen(md,max) {
      &:not(:first-child) {
        margin-top: em(45, $fz_base);
      }
    }
  }

  &__link {
    position: relative;
    text-transform: uppercase;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: em(-10, $fz_base);
      width: 0;
      height: 1px;
      background-color: $color-brend; 
      transition: width $ease-main;
    }

    @include screen(md,min) {
      &:hover {
        color: $color-brend;
  
        &::before {
          width: 100%;
        }
      } 
    }   
  }

  &__label {
    $fz: 22;
    font-size: em($fz, $fz_base);

    @include screen(md,max) {
      $fz: 35;
      font-size: em($fz, $fz_base);
    }
  }
}