.modal-cookie {
  display: none;
  position: fixed;
  right: 0;
  bottom: 0;
  max-width: em(400, $fz_base);
  padding: em(30 20, $fz_base);
  background-color: #fff;
  border-radius: em(20 20 0 0, $fz_base);
  z-index: 2;
  color: #000;

  &__title {
    $fz: 18;
    font-size: em($fz, $fz_base);
    margin-bottom: em(20, $fz_base);
    text-align: center;
  }

  &__wrap {
    display: flex;
    margin-top: em(20, $fz_base);

    .button {
      flex: 1;
      justify-content: center;

      &:not(:first-child) {
        margin-left: em(20, $fz_base);
      }
    }
  }
}