.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: em(10 0, $fz_base);
  z-index: 10;

  background-color: rgba(#1a2454, .6);

  &__order{
    @include screen(md,max) {
      &--first {
        order: 1;
      }

      &--second {
        order: 2;
      }

      &--third {
        order: 3;
      }
    }
  }

  &__button {
    &:not(:first-child) {
      margin-left: em(22, $fz_base);
    }
  }
}

.logo {
  display: block;
  position: relative;
  overflow: hidden;
  width: em(283, $fz_base);
  height: em(98, $fz_base);

  &__image {
    @include fill-img;
  }
}
