.tabs {
  $self: &;
  $tab-gap: 25;

  display: flex;
  margin-top: em(-$tab-gap, $fz_base);

  &__tab {
    display: inline-block;
    border-bottom: 1px solid transparent;
    transition: color $ease-main, border-color $ease-main;
    padding-bottom: em(10, $fz_base);
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 700;

    &:not(:first-child) {
      margin-left: em($tab-gap, $fz_base);
    }

    #{$self}__label {
      $fz: 25;
      font-size: em($fz, $fz_base);

      @include screen(md,max) {
        $fz: 20;
        font-size: em($fz, $fz_base);
      }
    }
    

    &.is-active {
      color: $color-brend;
      border-color: $color-brend;
    }
  }

  & + &-content {
    margin-top: em(30, $fz_base);
  }

  &-content {
    &__wrapper {
      display: none;
      opacity: 0;
      transition: opacity $ease-main, transform $ease-main;
      transform: translate3d(0, 10px, 0);

      &.is-active {
        display: block;
        opacity: 1;
        transform: translate3d(0, 0, 0);
      }
    }
  }
}
