.footer {
  $self: &;
  
  margin-top: auto;

  &__descr {
    padding: em(25 0, $fz_base);
    border-top: 1px solid rgba(#fff, 0.7);
    border-bottom: 1px solid rgba(#fff, 0.7);
    margin-bottom: em(100, $fz_base);

    #{$self}__label {
      $fz: 16;
      font-size: em($fz, $fz_base);
    }
  }

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: em(20, $fz_base);

    &:not(:first-child) {
      margin-top: em(20, $fz_base);
    }

    &:last-child {
      margin-bottom: em(80, $fz_base);
    }

    @include screen(md,max) {
      flex-direction: column;
    }
  }

  &__age {
    @include screen(md,max) {
      margin-top: em(40, $fz_base);
    }
  }

  &__social {
    @include screen(md,max) {
      margin: em(20 0, $fz_base);
    }
  }
}

.info {
  $self: &;

  &__item {
    display: flex;
    flex-direction: column;

    &:not(:first-child) {
      margin-top: em(35, $fz_base);
    }
  }

  &__title {
    font-weight: 700;
    text-transform: uppercase;

    #{$self}__label {
      $fz: 30;
      font-size: em($fz, $fz_base);
    }
  }

  &__descr {
    #{$self}__label {
      $fz: 24;
      font-size: em($fz, $fz_base);
    }
  }
}

.age {
  display: flex;
  align-items: center;
  justify-content: center;

  width: em(143, $fz_base);
  height: em(143, $fz_base);

  border-radius: 100%;
  border: 2px solid #fff;

  &__label {
    $fz: 70;
    font-size: em($fz, $fz_base);
  }
}

.social-list {
  $self: &;

  fill: #fff;
  display: flex;
  align-items: center;

  &__item {
    &:not(:first-child) {
      margin-left: em(20, $fz_base);
    }

    #{$self}--links & {
      &:not(:first-child) {
        margin-left: em(49, $fz_base);
      }
    }
  }

  &__link {
    &:hover {
      #{$self}__icon {
        fill: $color-brend;
      }
    }
  }

  &__icon {
    width: em(50, $fz_base);
    height: em(50, $fz_base);
    fill: inherit;
    transition: fill $ease-main;

    &--android {
      width: em(40, $fz_base);
      height: em(47, $fz_base);
    }

    &--ios {
      width: em(44, $fz_base);
      height: em(49, $fz_base);
    }

    &--linux {
      width: em(46, $fz_base);
      height: em(51, $fz_base);
    }

    &--windows {
      width: em(52, $fz_base);
      height: em(50, $fz_base);
    }

    &--fb {
      width: em(20, $fz_base);
      height: em(40, $fz_base);
    }

    &--google {
      width: em(34, $fz_base);
      height: em(36, $fz_base);
    }

    &--apple {
      width: em(33, $fz_base);
      height: em(40, $fz_base);
    }

    &--instagram {
      width: em(32, $fz_base);
      height: em(32, $fz_base);
    }
  }
}

.copyright {
  $self: &;
  
  background-color: $color-brend;
  padding: em(25 0, $fz_base);

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include screen(md,max) {
      flex-direction: column-reverse;
    }
  }

  &__text {
    #{$self}__label {
      $fz: 25;
      font-size: em($fz, $fz_base);
    }
  }
}

.footer-nav {
  $self: &;

  display: flex;
  align-items: center;

  @include screen(md,max) {
    flex-direction: column;
  }

  &__item {
    padding: em(0 40, $fz_base);
    
    &:not(:first-child) {
      border-left: 1px solid;
    }

    &:not(:last-child) {
      border-right: 1px solid;
    }

    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }

    @include screen(md,max) {
      padding: em(40 0, $fz_base) !important;
      border: 0 !important;
    }
  }
  
  &__link {
    display: block;
    width: 100%;
    height: 100%;
    text-decoration: underline;

    &:hover {
      color: darken($color-brend, 20%);
    }
  }

  &__label {
    $fz: 25;
    font-size: em($fz, $fz_base);
  }
}