.button {
  display: inline-block;
  color: #181C1F;
  min-width: em(110, $fz_base);
  padding: em(15 25, $fz_base);
  background-color: $color-brend;
  transition: background-color $ease-main;
  cursor: pointer;
  appearance: none;
  border-radius: em(25, $fz_base);
  text-transform: uppercase;
  font-weight: 700;

  &__label {
    $fz: 13;
    font-size: em($fz, $fz_base);
  }

  &[disabled] {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &:hover {
    background-color: darken($color-brend, 10%);
  }
}
