.container {
  width: em($container-width, $fz);
  padding-left: em($container-padding, $fz);
  padding-right: em($container-padding, $fz);
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  box-sizing: border-box;

  &--flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  @include screen(md,max) {
    $container-padding: 30;
    padding-left: em($container-padding, $fz);
    padding-right: em($container-padding, $fz);
  }
}